import React from "react";
import {
  StyledWrapper,
  StyledVideo,
  StyledContent,
  StyledImage,
} from "./styled";
const createMarkup = str => ({ __html: str });

// tags have a framework:something to detect the framework image
const getFramework = list =>
  list.find(tag => tag === "framework").replace(/framework:/, "");

const Videos = ({ videos }) => (
  <StyledWrapper>
    {videos &&
      videos.map(video => (
        <StyledVideo key={video.id}>
          <StyledImage>
            <img
              src={`../../../static/${getFramework(video.tags)}.png`}
              alt=""
            />
          </StyledImage>
          <StyledContent>
            <h3> {video.title}</h3>
            <div dangerouslySetInnerHTML={createMarkup(video.description)} />
          </StyledContent>
        </StyledVideo>
      ))}
  </StyledWrapper>
);

export default Videos;
